import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { selectSort } from '../redux/slices/filter/selectors';

import { setSelectedSort } from '../redux/slices/filter/slice';
import { SortNameEnum } from '../redux/slices/filter/types';

export type SortItem = { sortIndex: number; sortName: SortNameEnum };

export const sortList: SortItem[] = [
  { sortIndex: 0, sortName: SortNameEnum.TITLE },
  { sortIndex: 1, sortName: SortNameEnum.RATING },
  { sortIndex: 2, sortName: SortNameEnum.PRICE },
];

export const Sort: React.FC = React.memo(() => {
  const dispatch = useDispatch();
  const selectedSort = useSelector(selectSort);
  const sortRef = React.useRef<HTMLDivElement>(null);
  console.log(selectedSort);

  const [isVisible, setIsVisible] = React.useState(false);

  const onClickSelectSort = (index: number) => {
    setIsVisible(false);
    dispatch(setSelectedSort({ sortIndex: index, sortName: sortList[index].sortName }));
  };

  React.useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const _event = event as MouseEvent & {
        path: Node[];
      };
      if (!_event.path.includes(sortRef.current!)) {
        setIsVisible(false);
      }
    };
    document.body.addEventListener('click', handleClickOutside);
    return () => {
      document.body.removeEventListener('click', handleClickOutside);
    };
  }, []);

  return (
    <div ref={sortRef} className="sort">
      <div className="sort__label">
        <svg
          width="10"
          height="6"
          viewBox="0 0 10 6"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
            fill="#2C2C2C"
          />
        </svg>
        <b>Sort by:</b>
        <span onClick={() => setIsVisible(!isVisible)}>
          {sortList[selectedSort.sortIndex].sortName}
        </span>
      </div>
      {isVisible && (
        <div className="sort__popup">
          <ul>
            {sortList.map((val, index) => {
              return (
                <li
                  key={index}
                  className={selectedSort.sortIndex === index ? 'active' : ''}
                  onClick={() => {
                    onClickSelectSort(index);
                  }}
                >
                  {val.sortName}
                </li>
              );
            })}
          </ul>
        </div>
      )}
    </div>
  );
});
